<template>
    <el-card class="box-card">
        <div slot="header" class="clearfix">
            <span>第一步 添加节点信息</span>
        </div>
        <el-row>
            <el-col :span="12">
                <el-button type="primary" icon="el-icon-plus" @click="addNode" plain>添加节点</el-button>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="12">
                <table cellspacing="10px">
                    <tr style="text-align: left">
                        <th>
                            <p>序号</p>
                        </th>
                        <th>
                            <p>名称</p>
                        </th>
                        <th>
                            <p>需求</p>
                        </th>
                        <th>
                            <p>命令</p>
                        </th>
                    </tr>

                    <tr v-for="(node, index) in nodeInfo.length" :key="node" :index="index" class="ani">
                        <td style="text-align: center" class="ani">
                            <el-badge type="primary" :value="index === 0 ? '配送点' : index"/>
                        </td>
                        <td>
                            <el-input v-model="nodeInfo[index].name" :placeholder="index !== 0 ?  '节点' + (index) + '名称' : '配送点名称'"></el-input>
                        </td>
                        <td>
                            <p v-if="index === 0">0</p>
                            <el-input v-if="index !== 0" v-model="nodeInfo[index].need" :placeholder="index !== 0 ?  '节点' + (index) + '需求' : '配送点需求'" @input="nodeInfoWatcher"></el-input>
                        </td>
                        <td>
                            <el-button v-if="index !== 0" type="danger" icon="el-icon-remove-outline" plain @click="removeNode(index)"></el-button>
                        </td>

                    </tr>
                </table>
            </el-col>
        </el-row>
        <!--            <el-divider></el-divider>-->
        <!--            <el-row type="flex" justify="end">-->

        <!--                <el-button type="primary" icon="el-icon-check" @click="nodeFinish">完成</el-button>-->
        <!--            </el-row>-->

    </el-card>
</template>

<script>
    export default {
        name: "node",
        props: ['nodeInfo', 'pathInfo', 'canIUsePath'],

        methods: {
            addNode: function () {
                console.log(this.nodeInfo);
                this.$emit('pushNodeInfo');
                this.$emit('pushPathInfo');
                for(let i = 0; i < this.pathInfo[0].length; i++){
                    this.$emit('setPathInfo', this.nodeInfo.length - 1, i, '');
                    this.$emit('setPathInfo', i, this.nodeInfo.length - 1, '');
                }
            },
            nodeFinish: function () {
                if(!this.canIUsePath){
                    this.$message.error('有未完成的需求信息');
                }

                // let e = new Array();
                // for(let i = 0; i < this.nodeInfo.length; i++){
                //     e[i] = new Array();
                //     for(let j = 0; j < this.nodeInfo.length; j++){
                //         e[i][j] = '';
                //     }
                // }
                // this.pathInfo = e;

            },
            nodeInfoWatcher: function () {
                if(this.nodeInfo.length <= 1){
                    this.$emit('setCanIUsePath', false);
                    return;
                }
                for(let i = 0; i < this.nodeInfo.length; i++){
                    if(this.nodeInfo[i].need === ''){

                        this.$emit('setCanIUsePath', false);
                        return;
                    }
                }
                this.$emit('setCanIUsePath', true);
            },
            removeNode: function (index) {
                if(this.nodeInfo.length <= 1){
                    this.$message.error('不能再删除啦');
                    return;
                }
                this.$emit('removeNodeInfo', index);
            },

        },
        watch: {
            nodeInfo: function () {
                console.log(this.canIUsePath);
                this.nodeInfoWatcher();
            }
        }

    }
</script>

