<template>
    <div>

        <node class="ani" :node-info="nodeInfo" :path-info="pathInfo" @setPathInfo="setPathInfo" :can-i-use-path="canIUsePath" @setNodeInfo="setNodeInfo"
              @pushNodeInfo="pushNodeInfo" @pushPathInfo="pushPathInfo" @removeNodeInfo="removeNodeInfo" @setCanIUsePath="setCanIUsePath"></node>
        <br>
        <mypath0 v-if="pathIndex === 0" :path-index="pathIndex" class="ani" :path-info="pathInfo" :can-i-use-path="canIUsePath" :node-info="nodeInfo" @changeIndex="changeIndex" @rsetPathInfo="rsetPathInfo" @removePath="rsetPathInfo"></mypath0>
        <mypath1 v-if="pathIndex === 1" :path-index="pathIndex" class="ani" :path-info="pathInfo" :can-i-use-path="canIUsePath" :node-info="nodeInfo" @changeIndex="changeIndex"></mypath1>
        <mypath2 v-if="pathIndex === 2" :path-index="pathIndex" class="ani" :path-info="pathInfo" :can-i-use-path="canIUsePath" :node-info="nodeInfo" :xy-data="xyData" @changeIndex="changeIndex"></mypath2>
        <br>
        <truck class="ani" :truck-info="truckInfo" :can-i-use-path="canIUsePath" @removeTruckInfo="removeTruckInfo"
               @pushTruckInfo="pushTruckInfo" @setCanIUseLoad="setCanIUseLoad"></truck>
        <br>
        <personalize class="ani" :xy-data="xyData" :path-index="pathIndex" :truck-info="truckInfo" :path-info="pathInfo" :node-info="nodeInfo" :can-i-use-load="canIUseLoad" :can-i-use-path="canIUsePath" :can-i-use-finish="canIUseFinish" ></personalize>
<!--        <br>-->
<!--        <finish class="ani" :can-i-use-load="canIUseLoad" :can-i-use-path="canIUsePath" :can-i-use-finish="canIUseFinish"></finish>-->
    </div>
</template>

<script>
    import node from '../components/node';
    import mypath0 from '../components/path0';
    import mypath1 from '../components/path1';
    import mypath2 from '../components/path2';
    import truck from '../components/truck';
    import personalize from '../components/personalize';


    export default {
        components: {
            node,
            mypath0,
            mypath1,
            mypath2,
            truck,
            personalize
        },
        data(){
            return{
                canIUsePath: false,
                canIUseTruck: false,
                canIUseLoad: false,
                canIUseFinish: false,

                pathIndex: 0,
                isLoading: false,


                nodeInfo: [
                    {
                        name: '',
                        need: 0
                    }
                ],
                pathInfo: [200][200],
                xyData: [2][200],
                truckInfo: [{
                    load: '',
                    distance: '',
                    cost: ''
                }]
            }

        },
        mounted() {
            let e = new Array();
            for(let i = 0; i < 200; i++){
                e[i] = new Array();
                for(let j = 0; j < 200; j++){
                    e[i][j] = '';
                }
            }
            this.pathInfo = e;

            let f = new Array();
            for(let i = 0; i < 2; i++){
                f[i] = new Array();
                for(let j = 0; j < 200; j++){
                    f[i][j] = '';
                }
            }

            this.xyData = f;
        },
        methods:{
            setNodeInfo: function(n, val){
                this.nodeInfo[n] = val;
            },
            setPathInfo: function(m, n, val){
                this.pathInfo[m][n] = val;
            },
            rsetPathInfo: function(m, n, val){
                let temp = this.pathInfo[m];
                this.$set(temp, n, val);
                temp = this.pathInfo[n];
                this.$set(temp, m, val);

            },
            pushNodeInfo: function(){
                this.nodeInfo.push({
                    name: '',
                    need: ''

                });
            },
            pushPathInfo: function () {
                this.pathInfo[this.nodeInfo.length - 1].push([1][this.nodeInfo.length + 1]);
            },
            pushTruckInfo: function () {
                this.truckInfo.push({
                    load: '',
                    distance: '',
                    cost: ''

                });
            },
            setCanIUsePath(val){
                this.canIUsePath = val;
            },
            setCanIUseLoad(val){
                this.canIUseLoad = val;
            },
            removeNodeInfo: function (index) {
                this.nodeInfo.splice(index, 1);
            },
            removeTruckInfo: function (index) {
                this.truckInfo.splice(index, 1);
            },
            setCanIUseFinish: function (val) {
                this.canIUseFinish = val;
            },
            changeIndex: function (index) {
                this.pathIndex = index;
            }

        }
    }
</script>

