<template>
    <el-card class="box-card" v-if="canIUsePath">
        <div slot="header" class="clearfix">
            <span>第二步 编辑节点之间的路径</span>
            <el-button style="float: right; padding: 3px 5px" type="text" @click="changeIndex(0)">路径输入</el-button>
            <el-button style="float: right; padding: 3px 5px" type="text" @click="changeIndex(2)">坐标轴输入</el-button>
        </div>

        <table cellspacing="10px">
            <tr>
                <td></td>
                <td v-for="(node2, n) in nodeInfo" :key="n" :index="n">
                    <span><el-badge type="primary" :value="n === 0 ? '配送点' : n" style="margin-right: 10px" />{{nodeInfo[n].name}}</span>
                </td>
            </tr>
            <tr v-for="(node1, m) in nodeInfo" :key="m" :index="m">
                <td>
                    <span><el-badge type="primary" :value="m === 0 ? '配送点' : m" style="margin-right: 10px" />{{nodeInfo[m].name}}</span>
                </td>
                <td v-for="(node2, n) in nodeInfo" :key="n" :index="n">
                    <div v-if="m < n">
                        <el-input v-model="pathInfo[m][n]" placeholder="默认为无穷远"></el-input>
                    </div>
                    <div v-else-if="m === n">
                        {{0}}
                    </div>
                    <div v-else>
                        {{pathInfo[n][m].trim() === '' ? 0 : pathInfo[n][m]}}
                    </div>
                </td>
            </tr>
        </table>
    </el-card>
</template>

<script>
    export default {
        name: "mypath1",
        props: ['canIUsePath', 'pathInfo', 'nodeInfo', 'pathIndex'],
        data(){
            return{
                addPathFrom: '',
                addPathTo: ''
            }
        },
        methods: {
            removePath: function (m, n) {
                this.$emit('rsetPathInfo', m, n, '0');
                this.$message.success('删除成功');
            },
            addPath: function () {
                this.$emit('rsetPathInfo', this.addPathFrom - 1, this.addPathFrom - 1);
                this.addPathFrom = '';
                this.addPathTo = '';
            },
            changeIndex: function (index) {
                this.$emit('changeIndex', index);
            }

        }
    }
</script>

<style scoped>
    table{
        text-align: center;
    }

</style>