<template>
    <el-card class="box-card" v-if="canIUsePath">
        <div slot="header" class="clearfix">
            <span>第二步 编辑节点之间的路径</span>
            <el-button style="float: right; padding: 3px 5px" type="text" @click="changeIndex(1)">表格输入</el-button>
            <el-button style="float: right; padding: 3px 5px" type="text" @click="changeIndex(2)">坐标轴输入</el-button>
        </div>
        <el-row :gutter="20">
            <el-col :span="2">
                <el-input v-model="addPathFrom" placeholder="编号"></el-input>
            </el-col>
            <el-col :span="2" style="text-align: center; padding-top: 10px">
                <i class="el-icon-back" style="margin: 0 0 0 10px"></i>
                <i class="el-icon-right" style="margin: 0 10px 0 0"></i>
            </el-col>
            <el-col :span="2">

                <el-input v-model="addPathTo" placeholder="编号"></el-input>
            </el-col>
            <el-col :span="2">
                <el-button @click="addPath">添加路径</el-button>
            </el-col>
        </el-row>
        <el-divider></el-divider>

        <el-row :gutter="20"  v-for="(node1, m) in nodeInfo" :index="m" :key="m">
            <el-row :gutter="20"  v-for="(node2, n) in nodeInfo" :index="n" :key="n">
                <div v-if="m < n && pathInfo[m][n] !== '0'" class="ani">
                    <el-col :span="6" style="padding-top: 5px; margin-left: 30px">
                        <span><el-badge type="primary" :value="m === 0 ? '配送点' : m" style="margin-right: 10px" />{{nodeInfo[m].name}}</span>
                        <i class="el-icon-back" style="margin: 0 0 0 10px"></i>
                        <i class="el-icon-right" style="margin: 0 10px 0 0"></i>
                        <span><el-badge type="primary" :value="n" style="margin-right: 10px" />{{nodeInfo[n].name}}</span>
                    </el-col>
                    <el-col :span="6">
                        <el-input placeholder="距离（默认为无穷远）"  v-model="pathInfo[m][n]"></el-input>
                    </el-col>
                    <el-col :span="3">
                        <el-button type="danger" icon="el-icon-remove-outline" plain @click="removePath(m, n)"></el-button>
                    </el-col>
                </div>
                <div v-if="m < n && pathInfo[m][n] !== '0'" style="height: 60px">
                </div>
            </el-row>
        </el-row>
    </el-card>
</template>

<script>
    export default {
        name: "mypath0",
        props: ['canIUsePath', 'pathInfo', 'nodeInfo', 'pathIndex'],
        data(){
            return{
                addPathFrom: '',
                addPathTo: ''
            }
        },
        methods: {
            removePath: function (m, n) {
                this.$emit('rsetPathInfo', m, n, '0');
                this.$message.success('删除成功');
            },
            addPath: function () {
                if(this.addPathFrom.trim() === '' || this.addPathTo.trim() === ''){
                    this.$message.error('编号不能为空');
                    return;
                }

                this.$emit('rsetPathInfo', this.addPathFrom, this.addPathTo);
                this.addPathFrom = '';
                this.addPathTo = '';
            },
            changeIndex: function (index) {
                this.$emit('changeIndex', index);
            }


        }
    }
</script>

<style scoped>

</style>