<template>
    <el-card class="box-card" v-if="canIUsePath">
        <div slot="header" class="clearfix">
            <span>第三步 编辑车辆种类信息</span>
        </div>
        <el-row :gutter="20">
            <el-col :span="8" style="padding-top: 5px">
                <el-button type="primary" plain @click="addTruck">添加车辆种类</el-button>
            </el-col>
        </el-row>

        <el-row>
            <table cellspacing="10px">
                <tr style="text-align: left" class="ani">
                    <td>
                        <p>序号</p>
                    </td>
                    <td>
                        <p>最大载货量</p>
                    </td>
                    <td>
                        <p>最大里程数</p>
                    </td>
                    <td>
                        <p>车辆费用</p>
                    </td>
                    <td>
                        <p>命令</p>
                    </td>
                </tr>
                <tr v-for="(truck, index) in truckInfo" :key="index" :index="index" class="ani">
                    <td style="text-align: center">
                        <el-badge type="success" :value="index + 1"/>
                    </td>
                    <td>
                        <el-input v-model="truckInfo[index].load" @input="bindInput" placeholder="最大载货量"></el-input>
                    </td>
                    <td>
                        <el-input v-model="truckInfo[index].distance" @input="bindInput" placeholder="最大里程数"></el-input>
                    </td>
                    <td>
                        <el-input v-model="truckInfo[index].cost" @input="bindInput" placeholder="车辆费用"></el-input>
                    </td>
                    <td>
                        <el-button type="danger" icon="el-icon-remove-outline" plain @click="removeTruck(index)"></el-button>
                    </td>

                </tr>
            </table>
        </el-row>
    </el-card>

</template>

<script>
    export default {
        name: "truck",
        props: ['canIUsePath', 'truckInfo'],
        methods: {
            addTruck: function(){
                this.$emit('pushTruckInfo');
            },
            removeTruck: function (index) {
                if(this.truckInfo.length <= 1){
                    this.$message.error('不能再删除啦');
                    return;
                }
                this.$emit('removeTruckInfo', index);
            },
            truckFinish: function () {
                for(let i = 0; i < this.truckInfo.length; i++){
                    if(this.truckInfo[i].distance === '' || this.truckInfo[i].load === '' || this.truckInfo[i].cost === '') {
                        this.$message.error('有未填的车辆信息');

                        this.$emit('setCanIUseLoad', false);
                        return;
                    }
                }
                this.$emit('setCanIUseLoad', true);
            },
            bindInput: function () {
                console.log('www');
                for (let i = 0; i < this.truckInfo.length; i++){
                    if(this.truckInfo[i].distance === '' || this.truckInfo[i].load === '' || this.truckInfo[i].cost === '') {
                        this.$emit('setCanIUseLoad', false);
                        return;
                    }
                    this.$emit('setCanIUseLoad', true);
                }
            }

        }
    }
</script>

