<template>
    <el-card class="box-card" v-if="canIUseLoad && canIUsePath">
        <div slot="header" class="clearfix">
            <span>第四步 输入个性化参数</span>
        </div>
        <el-form label-width="180px">
            <el-form-item label="满载率影响参数">
                <el-col :span="3">
                    <el-input v-model="parameter.fullLoad" placeholder="0~100"></el-input>
                </el-col>
            </el-form-item>

            <el-form-item label="总路程影响参数">
                <el-col :span="3">
                    <el-input v-model="parameter.distance" placeholder="0~100"></el-input>
                </el-col>
            </el-form-item>

            <el-form-item label="总费用影响参数">
                <el-col :span="3">
                    <el-input v-model="parameter.cost" placeholder="0~100"></el-input>
                </el-col>
            </el-form-item>


            <el-form-item label="每个点的固定卸货时间">
                <el-col :span="3">
                    <el-input v-model="parameter.fixedDelay" placeholder="0~100"></el-input>
                </el-col>
            </el-form-item>

            <el-form-item label="车辆速度">
                <el-col :span="3">
                    <el-input v-model="parameter.carVel" placeholder="0~100"></el-input>
                </el-col>
            </el-form-item>

        </el-form>
        <el-row :gutter="20">
            <el-col :span="6">
            </el-col>
        </el-row>
        <el-divider></el-divider>
        <el-row type="flex" justify="end">

            <el-button type="primary" icon="el-icon-check" @click="finish">完成</el-button>
        </el-row>

    </el-card>
</template>

<script>
    export default {
        name: "personalize",
        props: ['nodeInfo', 'pathInfo', 'truckInfo', 'canIUseLoad', 'canIUsePath', 'canIUseFinish', 'pathIndex', 'xyData'],
        data(){
            return{
                parameter: {
                    fullLoad: '',
                    distance: '',
                    cost: '',
                    fixedDelay: '',
                    carVel: '',
                }
            }
        },
        methods: {
            finish: function () {
                if(this.parameter.fullLoad.trim() === ''
                    || this.parameter.distance.trim() === ''
                    || this.parameter.cost.trim() === ''
                    || this.parameter.fixedDelay.trim() ===''
                    || this.parameter.carVel.trim() === ''
                ){
                    this.$emit('setCanIUse', false);
                    this.$message.error('有未完成的信息哦');
                    return;
                }
                this.$emit('setCanIUse', true);


                let pathInfo = new Array(this.nodeInfo.length);

                for(let i = 0; i < this.nodeInfo.length; i++){
                    pathInfo[i] = new Array(this.nodeInfo.length);
                }


                if(this.pathIndex !== 2){
                    for(let i = 0; i < this.nodeInfo.length; i++){
                        for(let j = i; j< this.nodeInfo.length; j++){
                            let temp = this.pathInfo[i][j];
                            if(temp.trim() === '') {
                                temp = '0';
                            }
                            pathInfo[i][j] = temp;
                            pathInfo[j][i] = temp;
                        }
                    }
                }
                else{
                    //先检查
                    for(let i = 0; i < 2; i++){
                        for(let j = 0; j < this.nodeInfo.length; j++){
                            if(this.xyData[i][j].trim() === ''){
                                this.$message.error('有未填写完成的信息');
                                return;
                            }
                        }
                    }

                    for(let i = 0; i < this.nodeInfo.length; i++){
                        for(let j = i; j< this.nodeInfo.length; j++){
                            //距离公式
                            let temp = Math.sqrt(Math.pow(this.xyData[0][i] - this.xyData[0][j], 2) + Math.pow(this.xyData[1][i] - this.xyData[1][j], 2));
                            pathInfo[i][j] = temp;
                            pathInfo[j][i] = temp;
                        }
                    }
                }




                // console.log(pathInfo);
                // this.$router.push({
                //     name: 'result',
                //     params: {
                //         graph: pathInfo,
                //         node: this.nodeInfo,
                //         truck: this.truckInfo,
                //         load: this.load
                //     }
                // });

                const loading = this.$loading({
                    lock: true,
                    text: 'Loading',
                    spinner: 'el-icon-loading',
                    background: 'rgba(0, 0, 0, 0.7)'
                });


                this.axios({
                    method: 'post',
                    url: 'http://localhost:8081',
                    data: {
                        "graph": pathInfo,
                        "node": this.nodeInfo,
                        "truck": this.truckInfo,
                        "load": this.load,
                        "affectFullLoad": this.parameter.fullLoad,
                        "affectDistance": this.parameter.distance,
                        "affectCost": this.parameter.cost,
                        "fixTimeCost": this.parameter.fixedDelay,
                        "carVel": this.parameter.carVel
                    }
                }).
                then(res => {
                    console.log(res);
                    if(res.status !== 200){
                        this.$message.error('发生了不可描述的错误');
                        return;
                    }
                    loading.close();
                    this.$router.push({
                        name: 'result',
                        params: {
                            result: res,
                            truckInfo: this.truckInfo,
                            nodeInfo: this.nodeInfo
                        }
                    });
                }).
                catch(res => {
                    console.log(res);
                    loading.close();
                    this.$message.error('发生了不可描述的错误');

                });
            }
        }
    }
</script>

