<template>
    <el-card class="box-card" v-if="canIUsePath">
        <div slot="header" class="clearfix">
            <span>第二步 编辑节点之间的路径</span>
            <el-button style="float: right; padding: 3px 5px" type="text" @click="changeIndex(0)">路径输入</el-button>
            <el-button style="float: right; padding: 3px 5px" type="text" @click="changeIndex(1)">表格输入</el-button>
        </div>

        <table cellspacing="10px">
            <tr>
                <td>
                    序号
                </td>
                <td>
                    X坐标
                </td>
                <td>
                    Y坐标
                </td>
            </tr>
            <tr v-for="(node, n) in nodeInfo" :key="n" :index="n">
                <td style="text-align: center">
                    <span><el-badge type="primary" :value="n === 0 ? '配送点' : n" style="margin-right: 10px" />{{nodeInfo[n].name}}</span>
                </td>
                <td>
                    <el-input v-model="xyData[0][n]" placeholder="必填"></el-input>
                </td>
                <td>
                    <el-input v-model="xyData[1][n]" placeholder="必填"></el-input>
                </td>
            </tr>
        </table>
    </el-card>
</template>

<script>
    export default {
        name: "mypath2",
        props: ['canIUsePath', 'pathInfo','xyData', 'nodeInfo', 'pageIndex'],
        data(){
            return{
                addPathFrom: '',
                addPathTo: '',
            }
        },
        methods: {
            removePath: function (m, n) {
                this.$emit('rsetPathInfo', m, n, '0');
                this.$message.success('删除成功');
            },
            addPath: function () {
                this.$emit('rsetPathInfo', this.addPathFrom - 1, this.addPathFrom - 1);
                this.addPathFrom = '';
                this.addPathTo = '';
            },
            changeIndex: function (index) {
                this.$emit('changeIndex', index);
            }
        }
    }
</script>

